.main {
  padding: 75px 0 400px;
  background-image: url("../img/main.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__bottom {
    margin-top: 100px;
    &-logo {
      display: none;
    }
  }
  &__left {
    display: flex;
    align-items: center;
  }
  &__logo {
    width: 400px;
    display: block;
    img {
      width: 100%;
      height: auto;
    }
    &-mob {
      width: 200px;
      display: none;
      img {
        width: 100%;
        height: auto;
      }
    }
  }
  &__link {
    width: 178.93px;
    height: 74.61px;
    background: #FFC913;
    font-size: 36px;
    line-height: 105%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 100px;
  }
  &__btn {
    width: 360.59px;
    height: 74.61px;
    background: #000000;
    color: #ffffff;
    font-size: 20px;
    line-height: 105%;
    font-weight: 700;
    text-transform: uppercase;
  }
  &__title {
    font-size: 105px;
    line-height: 120%;
    font-weight: 700;
    text-transform: uppercase;
  }
  &__triangles {
    max-width: 380px;
    text-align: center;
  }
}
.timer {
  background-image: url("../img/timer.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 170px 0 325px;
  margin-top: -100px;
  position: relative;
  z-index: 1;
  &__top {
    display: flex;
    justify-content: space-between;
  }
  &__time {
    display: flex;
    justify-content: center;
    align-items: center;
    //max-width: 1050px;
    margin: 140px auto 0;
  }
  &__logo {
    display: block;
    width: 406px;
    &-mob {
      display: none;
    }
    img {
      width: 100%;
    }
  }
  &__title {
    font-size: 78px;
    line-height: 120%;
    font-weight: 700;
    text-transform: uppercase;
    color: #ffffff;
  }
  &__day {
    position: relative;
    padding-right: 170px;
    &:after {
      content: '';
      position: absolute;
      top: 30px;
      right: 85px;
      width: 20px;
      height: 20px;
      background: #000000;
      border-radius: 50%;
    }
    &:before {
      content: '';
      position: absolute;
      top: 60px;
      right: 85px;
      width: 20px;
      height: 20px;
      background: #000000;
      border-radius: 50%;
    }
    .day {
      width: 115px;
      font-size: 105px;
      line-height: 105%;
      font-weight: 700;
      &-desc {
        font-size: 20px;
        line-height: 105%;
        font-weight: 700;
      }
    }
  }
  &__hour {
    position: relative;
    padding-right: 170px;
    &:after {
      content: '';
      position: absolute;
      top: 30px;
      right: 85px;
      width: 20px;
      height: 20px;
      background: #000000;
      border-radius: 50%;
    }
    &:before {
      content: '';
      position: absolute;
      top: 60px;
      right: 85px;
      width: 20px;
      height: 20px;
      background: #000000;
      border-radius: 50%;
    }
    .hr {
      width: 115px;
      font-size: 105px;
      line-height: 105%;
      font-weight: 700;
      &-desc {
        font-size: 20px;
        line-height: 105%;
        font-weight: 700;
      }
    }
  }
  &__minute {
    position: relative;
    padding-right: 170px;
    &:after {
      content: '';
      position: absolute;
      top: 30px;
      right: 85px;
      width: 20px;
      height: 20px;
      background: #000000;
      border-radius: 50%;
    }
    &:before {
      content: '';
      position: absolute;
      top: 60px;
      right: 85px;
      width: 20px;
      height: 20px;
      background: #000000;
      border-radius: 50%;
    }
    .min {
      width: 115px;
      font-size: 105px;
      line-height: 105%;
      font-weight: 700;
      &-desc {
        font-size: 20px;
        line-height: 105%;
        font-weight: 700;
      }
    }
  }
  &__seconds {
    .sec {
      width: 115px;
      font-size: 105px;
      line-height: 105%;
      font-weight: 700;
      &-desc {
        font-size: 20px;
        line-height: 105%;
        font-weight: 700;
      }
    }
  }
}
.info {
  padding: 150px 0;
  position: relative;
  overflow: hidden;
  &__img-bg {
    position: absolute;
    z-index: 0;
    top: 80px;
    left: 0;
    pointer-events: none;
  }
  &__inner {
    max-width: 1080px;
    margin: 0 auto;
    position: relative;
    z-index: 1;
  }
  &__text {
    p {
      font-size: 26px;
      line-height: 150%;
      font-weight: 700;
      &:not(:first-child) {
        margin-top: 60px;
      }
    }
  }
  &__subtitle {
    font-size: 24px;
    line-height: 120%;
    font-weight: 700;
    margin-top: 60px;
    text-transform: uppercase;
  }
  &__desc {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 110px;
    &-left {
      display: flex;
      align-items: center;
    }
  }
  &__logo {
    width: 225px;
    display: block;
    img {
      width: 100%;
      height: auto;
    }
  }
  &__link {
    width: 142px;
    height: 60px;
    background: #FFC913;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 30px;
    line-height: 120%;
    margin-left: 60px;
  }
  &__doc {
    display: flex;
    align-items: center;
    i {
      width: 80px;
      img {
        width: 100%;
        height: auto;
      }
    }
    &-desc {
      font-size: 24px;
      line-height: 120%;
      font-weight: 700;
      text-transform: uppercase;
      margin-left: 25px;
      span {
        position: relative;
        &:after {
          content: '';
          position: absolute;
          left: 0;
          bottom: -5px;
          width: 100%;
          height: 3px;
          background: #000000;
        }
      }
    }
  }
  &__title {
    font-size: 58px;
    line-height: 120%;
    font-weight: 700;
    text-transform: uppercase;
    margin-top: 125px;
    br {
      display: none;
    }
  }
  &__btn {
    width: 447px;
    height: 92px;
    background: #FFC913;
    font-size: 24px;
    line-height: 105%;
    font-weight: 700;
    text-transform: uppercase;
    margin: 100px auto 0;
  }
}
.video {
  padding: 140px 0 180px;
  background: #000000;
  &__title {
    font-size: 58px;
    line-height: 120%;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
    color: #ffffff;
  }
  &__teaser {
    width: 1080px;
    height: 600px;
    margin: 110px auto 0;
