.footer {
  background: #F1F1F1;
  padding: 15px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__right {
    display: flex;
    align-items: center;
  }
  &__logo {
    display: block;
    width: 215px;
    img {
      width: 100%;
      height: auto;
    }
  }
  &__subtitle {
    font-size: 18px;
    line-height: 120%;
  }
  &__tel {
    font-size: 18px;
    line-height: 120%;
    font-weight: 500;
    margin-left: 70px;
  }
  &__socials {
    display: flex;
    align-items: center;
    margin-left: 35px;
  }
  &__social {
    &:not(:first-child) {
      margin-left: 18px;
    }
  }
}

@media screen and (max-width: 992px) {
  .footer {
    &__tel {
      margin-left: 25px;
    }
    &__socials {
      margin-left: 25px;
    }
  }
}

@media screen and (max-width: 768px) {
  .footer {
    &__socials {
      display: none;
    }
  }
}

@media screen and (max-width: 600px) {
  .footer {
    &__logo {
      width: 88px;
    }
    &__subtitle {
      font-size: 12px;
    }
    &__tel {
      font-size: 12px;
      margin-left: 15px;
    }
  }
}