.header {
  background: #000000;
  color: #ffffff;
  padding: 15px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__left {
    display: flex;
    align-items: center;
  }
  &__right {
    display: flex;
    align-items: center;
  }
  &__logo {
    width: 215px;
    display: block;
    img {
      width: 100%;
      height: auto;
    }
  }
  &__date {
    font-size: 18px;
    line-height: 120%;
    margin-left: 45px;
  }
  &__list {
    display: flex;
    align-items: center;
    li {
      font-size: 18px;
      line-height: 120%;
      text-transform: uppercase;
      &:not(:first-child) {
        margin-left: 70px;
      }
      &:hover {
        color: #FFC913;
      }
    }
  }
  &__tel {
    font-size: 18px;
    line-height: 120%;
    font-weight: 500;
    &:hover {
      color: #FFC913;
    }
  }
  &__socials {
    display: flex;
    align-items: center;
    margin-left: 35px;
  }
  &__social {
    &:not(:first-child) {
      margin-left: 18px;
    }
  }
  &__burger {
    position: relative;
    z-index: 5;
    width: 30px;
    height: 20px;
    cursor: pointer;
    display: none;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 70px;
      height: 70px;
      background: #FFFFFF;
      border-radius: 50%;
    }
    span {
      position: absolute;
      z-index: 2;
      background: #000000;
      width: 100%;
      height: 5px;
      transition: all 0.3s;
      &:nth-child(1) {
        top: 0;
        left: 0;
      }
      &:nth-child(2) {
        top: 50%;
        left: 0;
        transform: translate(0, -50%);
      }
      &:nth-child(3) {
        left: 0;
        bottom: 0;
      }
    }
    &--active {
      span {
        &:nth-child(1) {
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%) rotate(-45deg);
        }
        &:nth-child(2) {
          display: none;
        }
        &:nth-child(3) {
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%) rotate(45deg);
        }
      }
    }
  }
}

@media screen and (max-width: 1550px) {
  .header {
    &__date {
      margin-left: 25px;
    }
    &__list {
      li {
        font-size: 17px;
        &:not(:first-child) {
          margin-left: 30px;
        }
      }
    }
    &__socials {
      margin-left: 25px;
    }
  }
}

@media screen and (max-width: 1300px) {
  .header {
    background: transparent;
    position: fixed;
    z-index: 2;
    width: 100%;
    padding: 0;
    &__wrap {
      position: absolute;
      z-index: 4;
      top: 0;
      left: -105%;
      width: 100%;
      height: 100vh;
      padding: 150px 30px 30px;
      background: #000000;
      text-align: center;
      transition: all 0.5s;
      &--active {
        left: 0;
      }
    }
    &__burger {
      display: block;
      margin: 50px 15px 0 auto;
    }
    &__inner {
      justify-content: start;
      flex-direction: column;
      overflow-y: auto;
      height: 100%;
      &::-webkit-scrollbar {
        width: 3px;
      }
      &::-webkit-scrollbar-track {
        background: #ffffff;
        border-radius: 3px;
      }
      &::-webkit-scrollbar-thumb {
        background: #FFC913;
        border-radius: 3px;
      }
    }
    &__left {
      display: block;
    }
    &__center {
      margin-top: 50px;
    }
    &__right {
      display: block;
      margin-top: 50px;
    }
    &__date {
      margin: 25px 0 0 0;
    }
    &__list {
      display: block;
      li {
        &:not(:first-child) {
          margin: 25px 0 0 0;
        }
      }
    }
    &__socials {
      margin: 25px 0 0 0;
      justify-content: center;
    }
  }
}